import React, { useState } from 'react';
import StarRating from './StarRating';
import './ProductInfo.css';

const ProductInfo = ({ product, quantity, setQuantity, addToCart , selectedColor , setSelectedColor ,selectedSize, setSelectedSize}) => {
  const hasColors = product?.variations?.color?.length > 0;
  const hasSizes = product?.variations?.size?.length > 0;


  const getColorObj = () =>
    hasColors ? product.variations.color.find(c => c.name === selectedColor) : null;
  const getSizeObj = () =>
    hasSizes ? product.variations.size.find(s => s.name === selectedSize) : null;

  const basePrice = product.price;
  const colorPrice = getColorObj()?.price || 0;
  const sizePrice = getSizeObj()?.price || 0;
  const finalPrice = (basePrice + colorPrice + sizePrice).toFixed(2);

  const handleQuantityChange = (change) => {
    // handle quantity both minus and plus
    setQuantity((prev) => {
      const newQuantity = prev + change;
      return newQuantity < 1 ? 1 : newQuantity > 99 ? 99
        : newQuantity;
    });
  };

  return (
    <div className="product-info-singleproduct">
      <div className="product-badge">
        <span className="dress-badge">Dress</span>
      </div>

      <div className="product-rating">
        <StarRating rating={product.rating} readOnly={true} />
        <span className="review-count">{product.reviewCount} Reviews</span>
      </div>

      <h1 className="product-title">{product.name}</h1>
      <div className="stock-status">
        <span className="in-stock">
          {hasColors ? (getColorObj()?.stock > 0 ? "In Stock" : "Out of Stock") : "In Stock"}
        </span>
      </div>

      <div className="product-pricing">
        <span className="original-price">${product.originalPrice}</span>
        <span className="current-price">${finalPrice}</span>
      </div>

     

      {/* Color Pack */}
      {hasColors && (
        <div className="variation-section">
          <label>Color:</label>
          <div className="color-options-singleproduct">
            {product.variations.color.map((color) => (
              <button
                key={color.name}
                className={`color-swatch ${selectedColor === color.name ? 'selected' : ''}`}
                style={{ backgroundColor: color.hex }}
                onClick={() => setSelectedColor(color.name)}
                title={color.name}
              />
            ))}
          </div>
        </div>
      )}

      {/* Size Options */}
      {hasSizes && (
        <div className="variation-section">
          <label>Size:</label>
          <div className="size-box-group">
            {product.variations.size.map((size) => (
              <button
                key={size.name}
                className={`size-box ${selectedSize === size.name ? 'selected' : ''}`}
                onClick={() => setSelectedSize(size.name)}
              >
                {size.name}
                {size.price > 0 && <span className="extra-price"> +${size.price}</span>}
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Quantity */}
      <div className="quantity-section">
        {/* <div className="quantity-controls">
          <button
            className="quantity-btn"
            onClick={() => handleQuantityChange(-1)}
            disabled={quantity <= 1}
          >
            -
          </button>
          <span className="quantity-display">{quantity.toString().padStart(2, '0')}</span>
          <button className="quantity-btn" onClick={() => handleQuantityChange(1)}>
            +
          </button>
        </div> */}

        <button className="add-to-cart-btn" onClick={addToCart}>
          <span className="cart-icon">ðŸ›’</span> Add to Cart
        </button>
      </div>

      {/* Meta Info */}
      <div className="product-meta">
        <div className="meta-item">
          <span className="meta-label">SKU:</span>
          <span className="meta-value">{product.sku}</span>
        </div>
        <div className="meta-item">
          <span className="meta-label">Categories:</span>
          <span className="meta-value">{product.categories.join(', ')}</span>
        </div>
        <div className="meta-item">
          <span className="meta-label">Tags:</span>
          <span className="meta-value">{product.tags.join(', ')}</span>
        </div>
      </div>

      {/* Share */}
      <div className="share-section">
        <span className="share-label">Share:</span>
        <div className="share-buttons">
          <button className="share-btn facebook">f</button>
          <button className="share-btn twitter">t</button>
          <button className="share-btn linkedin">in</button>
          <button className="share-btn pinterest">p</button>
          <button className="share-btn email">@</button>
        </div>
      </div>
    </div>
  );
};

export default ProductInfo;
