import React, { useState } from 'react';
import Breadcrumb from './Breadcrumb';
import ProductGallery from './ProductGallery';
import ProductInfo from './ProductInfo';
import ProductTabs from './ProductTabs';
import RelatedProducts from './RelatedProducts';
import './ProductPage.css';
import { useGlobal } from '../context/GlobalContext';
import { notify } from '../shared/Notification/Notificaiton';

const ProductPage = () => {
  const [activeTab, setActiveTab] = useState('description');
  const [quantity, setQuantity] = useState(1);
  const [selectedColor, setSelectedColor] = useState(null);
  const [selectedSize, setSelectedSize] = useState(null);

  const { dispatch, state } = useGlobal();

  const productData = {
    id: 'BQTD004X5L',
    name: 'Wide Cotton Tunic Dress',
    price: 7.25,
    originalPrice: 9.95,
    rating: 5,
    reviewCount: 10,
    inStock: true,
    sku: 'BQTD004X5L',
    categories: ['T-Shirts', 'Tops', 'Mens'],
    tags: ['fashion', 't-shirt', 'Men'],
    description: `
     Complete School Supply Kit

    
Magic School Box has introduced the Complete School Supply Kit SP25. Itâ€™s a modern solution that includes everything your child needs for the school yearâ€”pencils, notebooks, markers, and moreâ€”so you wonâ€™t have to run from store to store. Order now at the best value and save time and hassle.

Magic School Box has introduced the Complete School Supply Kit SP25. This thoughtfully curated bundle guarantees quality and convenience, ensuring your student is prepared from day one. Buy with confidence and enjoy a stress-free back-to-school season.

In marketing, an all-in-one school supply kit is an offering made available to parents and students to satisfy the need for convenience and preparedness. In retail, such kits are treated as bundled merchandise, while in manufacturing, they are assembled from raw materialsâ€”paper, plastic, and metalâ€”and packaged as finished goods. Educational services, such as personalized labeling and online ordering platforms, are also regarded as part of the product offering. School suppliesâ€”traditionally purchased separatelyâ€”are now widely available in consolidated packs, simplifying purchase decisions and ensuring consistency. In project management for school events, these kits define the deliverables by providing the items needed to achieve classroom objectives.
    `, 
       images: [
      'https://weiboo.pixcelsthemes.com/weiboo/assets/images/products/product-filt1.jpg',
      'https://images.pexels.com/photos/1065084/pexels-photo-1065084.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1462637/pexels-photo-1462637.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1483771/pexels-photo-1483771.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1536619/pexels-photo-1536619.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1065084/pexels-photo-1065084.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1462637/pexels-photo-1462637.jpeg?auto=compress&cs=tinysrgb&w=600',
      'https://images.pexels.com/photos/1483771/pexels-photo-1483771.jpeg?auto=compress&cs=tinysrgb&w=600'
      
    ],

    variations: {
      color: [
        { name: 'Red', hex: '#ff0000', stock: 5, price: 0 },
        { name: 'Blue', hex: '#0000ff', stock: 4, price: 0 },
        { name: 'Black', hex: '#000000', stock: 3, price: 1.5 },
      ],
      size: [
        { name: 'Small', price: 0 },
        { name: 'Medium', price: 2 },
        { name: 'Large', price: 5 },
      ]
    }
  };

const addToCart = () => {
  const hasColors = productData?.variations?.color?.length > 0;
  const hasSizes = productData?.variations?.size?.length > 0;

  // Validation based on what exists
  if (hasColors && !selectedColor) {
    notify("error", "Please select a color.");
    return;
  }

  if (hasSizes && !selectedSize) {
    notify("error", "Please select a size.");
    return;
  }

  const colorOption = hasColors
    ? productData.variations.color.find(c => c.name === selectedColor)
    : null;

  const sizeOption = hasSizes
    ? productData.variations.size.find(s => s.name === selectedSize)
    : null;

  const finalPrice =
    productData.price +
    (colorOption?.price || 0) +
    (sizeOption?.price || 0);

  const cartItem = {
    ...productData,
    selectedColor: hasColors ? selectedColor : null,
    selectedSize: hasSizes ? selectedSize : null,
    quantity,
    price: finalPrice,
    id: `${productData.id}_${hasColors ? selectedColor : 'no-color'}_${hasSizes ? selectedSize : 'no-size'}`
  };

  const isDuplicate = state.cart.some(item => item.id === cartItem.id);
  if (isDuplicate) {
    notify("error", "Product with selected options already in cart!");
    return;
  }

  const updatedCart = [...state.cart, cartItem];

  localStorage.setItem(
    "globalState",
    JSON.stringify({ ...state, cart: updatedCart })
  );

  dispatch({ type: "ADD_TO_CART", payload: cartItem });
  notify("success", "Product added to cart!");
};


  return (
    <div className="product-page">
      <div className="container">
        <Breadcrumb />

        <div className="product-section">
          <div className="product-content">
            <ProductGallery images={productData.images} />
            <ProductInfo
              product={productData}
              quantity={quantity}
              setQuantity={setQuantity}
              addToCart={addToCart}
              selectedColor={selectedColor}
              setSelectedColor={setSelectedColor}
              selectedSize={selectedSize}
              setSelectedSize={setSelectedSize}
            />
          </div>
        </div>

        <ProductTabs
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          description={productData.description}
        />

        <RelatedProducts />
      </div>
    </div>
  );
};

export default ProductPage;
