import React from 'react';
import './StarRating.css';
import StarRatings from 'react-star-ratings';

const StarRating = ({ rating, setRating, maxRating = 5, readOnly = false }) => {
  const handleRatingChange = (newRating) => {
    if (!readOnly && setRating) {
      setRating(newRating);
    }
  };

  return (
    <div className="star-rating">
      <StarRatings
        rating={rating}
        starRatedColor="#FFA829"
        starHoverColor={readOnly ? null : "#FFA829"}
        changeRating={readOnly ? null : handleRatingChange}
        numberOfStars={maxRating}
        name="user-rating"
      />
    </div>
  );
};

export default StarRating;
